.social__list {
  @include list-reset;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.social__icon {
  fill: $pink;
  position: absolute;

  &--youtube {
    top: calc(50% - 7px);
    left: calc(50% - 7px);
  }

  &--twitter {
    top: calc(50% - 9px);
    left: calc(50% - 8px);
  }

  &--fb {
    top: calc(50% - 10px);
    left: calc(50% - 4px);
  }
}

.social__item {
  position: relative;
  margin-right: 17px;
  border: 3.0px solid $gray-light;
  border-radius: 50%;
  width: 46px;
  height: 46px;

  &:hover {
    border: 3.0px solid $gray-font;
  }

  &:active {
    opacity: 0.1;
  }
}




